import { Maybe, Just, Nothing } from "./Maybe";

import { kernelUpdate, kernelFromList, kernelToList } from "./Dict_kernel";

export { get };
export { update };
export { values };
export { fromList };
export { toList };

function get<a, b>(key: a, dict: any): Maybe<b> {
    const value: b = dict[key];
    if (value === undefined) {
        return Nothing({ });
    } else {
        return Just({ value });
    }
}

function update<a, v>(a: a, fn: (arg0: Maybe<v>) => Maybe<v>, dict: any): any {
    return kernelUpdate(a, fn, dict);
}

function values(dict: any): any[] {
    return Object.values(dict);
}

type ListItem = {
    key: any;
    value: any;
}

function ListItem(args: { key: any, value: any }): ListItem {
    return {
        ...args,
    };
}

function fromList(items: ListItem[]): any {
    return kernelFromList(items);
}

function toList(dict: any): ListItem[] {
    return kernelToList(dict);
}
