import { HtmlNode, RunningProgram, div, button, input, a, h3, p, onInput, onClick, text, program, attribute, style_ } from "../derw-packages/derw-lang/html/src/Html";

import { split, join, fromNumber } from "../derw-packages/derw-lang/stdlib/src/String";

import * as List from "../derw-packages/derw-lang/stdlib/src/List";

import * as Maybe from "../derw-packages/derw-lang/stdlib/src/Maybe";

import * as Dict from "../derw-packages/derw-lang/stdlib/src/Dict";

type Unread = {
    kind: "Unread";
};

function Unread(args: {}): Unread {
    return {
        kind: "Unread",
        ...args,
    };
}

type Correct = {
    kind: "Correct";
};

function Correct(args: {}): Correct {
    return {
        kind: "Correct",
        ...args,
    };
}

type Incorrect = {
    kind: "Incorrect";
};

function Incorrect(args: {}): Incorrect {
    return {
        kind: "Incorrect",
        ...args,
    };
}

type AnswerStatus = Unread | Correct | Incorrect;

type Model = {
    dict: any;
}

function Model(args: { dict: any }): Model {
    return {
        ...args,
    };
}

const initModel: Model = Dict.fromList(List.map(function(index: any) {
    return {
    key: index,
    value: Unread({ })
};
}, Array.from({ length: 30 - 1 + 1 }, (x, i) => i + 1)));

type SetCorrect = {
    kind: "SetCorrect";
    value: number;
};

function SetCorrect(args: { value: number }): SetCorrect {
    return {
        kind: "SetCorrect",
        ...args,
    };
}

type SetIncorrect = {
    kind: "SetIncorrect";
    value: number;
};

function SetIncorrect(args: { value: number }): SetIncorrect {
    return {
        kind: "SetIncorrect",
        ...args,
    };
}

type SetUnread = {
    kind: "SetUnread";
    value: number;
};

function SetUnread(args: { value: number }): SetUnread {
    return {
        kind: "SetUnread",
        ...args,
    };
}

type Msg = SetCorrect | SetIncorrect | SetUnread;

function justCorrect(x: any): AnswerStatus {
    return Correct({ });
}

function justIncorrect(x: any): AnswerStatus {
    return Incorrect({ });
}

function justUnread(x: any): AnswerStatus {
    return Unread({ });
}

function update(msg: Msg, model: Model): Model {
    const _res = msg;
    switch (_res.kind) {
        case "SetCorrect": {
            const { value } = _res;
            return Dict.update(value, function(x: any) {
            return Maybe.map(justCorrect, x);
        }, model);
        }
        case "SetIncorrect": {
            const { value } = _res;
            return Dict.update(value, function(x: any) {
            return Maybe.map(justIncorrect, x);
        }, model);
        }
        case "SetUnread": {
            const { value } = _res;
            return Dict.update(value, function(x: any) {
            return Maybe.map(justUnread, x);
        }, model);
        }
    }
}

function getColor(s: AnswerStatus): string {
    const _res = s;
    switch (_res.kind) {
        case "Unread": {
            return "blue";
        }
        case "Correct": {
            return "#52D017";
        }
        case "Incorrect": {
            return "red";
        }
    }
}

type Pair = {
    key: number;
    value: any;
}

function Pair(args: { key: number, value: any }): Pair {
    return {
        ...args,
    };
}

function makeRectangle(answer: Pair): HtmlNode<Msg> {
    return div([ ], [ style_("width", "140px"), style_("height", "100px"), style_("margin-top", "10px"), style_("padding-top", "5px"), style_("padding-left", "5px"), style_("background-color", getColor(answer.value)), style_("border", "2px solid black") ], [ button([ onClick(function(_: any) {
        return SetCorrect({ value: answer.key });
    }) ], [ ], [ text("Yes") ]), text(" "), button([ onClick(function(_: any) {
        return SetIncorrect({ value: answer.key });
    }) ], [ ], [ text("No") ]), text(" "), button([ onClick(function(_: any) {
        return SetUnread({ value: answer.key });
    }) ], [ ], [ text("Reset") ]) ]);
}

function isCorrect(answer: AnswerStatus): boolean {
    const _res = answer;
    switch (_res.kind) {
        case "Correct": {
            return true;
        }
        default: {
            return false;
        }
    }
}

function isIncorrect(answer: AnswerStatus): boolean {
    const _res = answer;
    switch (_res.kind) {
        case "Incorrect": {
            return true;
        }
        default: {
            return false;
        }
    }
}

function isUnread(answer: AnswerStatus): boolean {
    const _res = answer;
    switch (_res.kind) {
        case "Unread": {
            return true;
        }
        default: {
            return false;
        }
    }
}

function stats(model: Model): HtmlNode<Msg> {
    const correct: string = fromNumber(List.length(List.filter(isCorrect, Dict.values(model))));
    const incorrect: string = fromNumber(List.length(List.filter(isIncorrect, Dict.values(model))));
    const unread: string = fromNumber(List.length(List.filter(isUnread, Dict.values(model))));
    return text(" Correct: " + correct + " | Incorrect: " + incorrect + " | Unread: " + unread);
}

function view(model: Model): HtmlNode<Msg> {
    return div([ ], [ style_("padding", "10px") ], [ h3([ ], [ ], [ text("Jeopardy! Heatmap") ]), p([ ], [ style_("font-size", "12px") ], [ text("this is by msszczep on Github, rewritten in Derw as an example of Derw") ]), p([ ], [ ], [ a([ ], [ attribute("href", "http://www.szcz.org/jeopardyheatmap.html") ], [ text("Original is here.") ]) ]), p([ ], [ ], [ a([ ], [ attribute("href", "https://github.com/msszczep/jeopardy-heatmap/") ], [ text("Copy of Elm source code is here.") ]) ]), p([ ], [ ], [ a([ ], [ attribute("href", "https://github.com/derw-lang/examples/") ], [ text("Copy of Derw source code is here.") ]) ]), p([ ], [ ], [ stats(model) ]), div([ ], [ style_("display", "grid"), style_("grid-template-columns", "auto auto auto auto auto auto"), style_("padding", "10px"), style_("width", "80%") ], List.map(makeRectangle, Dict.toList(model))) ]);
}

const root: any = document.getElementById("root");

const main: RunningProgram<Model, Msg> = program({
    initialModel: initModel,
    update,
    view,
    root
});
